@mixin keyframes($animation_name) {
    @-webkit-keyframes #{$animation_name} {
        @content;
    }

    @-moz-keyframes #{$animation_name} {
        @content;
    }

    @-o-keyframes #{$animation_name} {
        @content;
    }

    @keyframes #{$animation_name} {
        @content;
    }
}

@mixin animation($delay, $duration, $animation, $direction: normal, $fillmode: forwards) {
    -webkit-animation-delay: $delay;
    -webkit-animation-duration: $duration;
    -webkit-animation-name: $animation;
    -webkit-animation-fill-mode: $fillmode;
    -webkit-animation-direction: $direction;

    -moz-animation-delay: $delay;
    -moz-animation-duration: $duration;
    -moz-animation-name: $animation;
    -moz-animation-fill-mode: $fillmode;
    -moz-animation-direction: $direction;

    animation-delay: $delay;
    animation-duration: $duration;
    animation-name: $animation;
    animation-fill-mode: $fillmode;
    animation-direction: $direction;
}

@mixin slideInMenuTray($tray-length) {
    @include keyframes(slideInMenu) {
        0% {
            visibility: hidden;
            transform: translateX($tray-length);
        }

        1% {
            visibility: visible;
        }

        100% {
            transform: translateX(0);
        }
    }

    @include animation(0s, 200ms, slideInMenu);
}

@mixin slideOutMenuTray($tray-length) {
    @include keyframes(slideOutMenu) {
        0% {
            visibility: visible;
            transform: translateX(0);
        }
        
        99% {
            transform: translateX($tray-length);
        }

        100% {
            visibility: hidden
        }
    }

    @include animation(0s, 200ms, slideOutMenu);
}
